

html, body, div, ul, li{
   margin: 0;
   padding: 0;
   font-family: '楷体';
   font-size: 18px;
}

li {
   list-style: none;
}

a {
   text-decoration: none;
   font-size: 16px;
}

a:hover {
   text-decoration: none;
   color: white;
}

input, button {
   padding: 0 15px 0;
   border: none;
   border-radius: 3px;
}

body {
   background-image: url(./img/bg.jpg);
   background-repeat: no-repeat;
   background-size: cover;
}

#root {
   width: 70%;
   min-width: 785px;
   margin: 0 auto;
   background-repeat: no-repeat;
}

.App {
   text-align: center;
}

