.login {
   position: fixed;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   background: rgb(12, 12, 12, .3);
}

.login .content {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   display: inline-block;
   padding: 15px 25px;
   border-radius: 8px;
   background: #ccc;
}

.login .content div,
.login .content button {
   margin-top: 8px;
}

.login .content .cancel {
   position: absolute;
   right: 5px;
   top: 5px;
   margin-top: 0;
   width: 20px;
   height: 20px;
   cursor: pointer;
}


.login .content .cancel::before, 
.login .content .cancel::after {
   content: '';
   position: absolute;
   top: 50%;
   left: 50%;
   height: 25px;
   width: 2px;
   background: black;
}

.login .content .cancel::before{
   transform: translate(-50%, -50%) rotate(45deg);
}
.login .content .cancel::after{
   transform: translate(-50%, -50%) rotate(-45deg);
}


.login .content .title {
   margin-bottom: 30px;
}

.login .content .confirm canvas {
   background: white;
   vertical-align: middle;
}

.login .content .confirm span {
   padding-left: 8px;
   vertical-align: middle;
}

.login .content .confirm input {
   margin-top: 14px;
}

.login .content .over{
   box-shadow: 0 0 2px 1px #333;
}

.login .content .over:hover {
   box-shadow: 0 0 2px 1px #333 inset;
   font-size: 17.5px;
}

.login .content .over:focus{
   border: none;
   outline: none;
}