

html, body, div, ul, li{
   margin: 0;
   padding: 0;
   font-family: '\6977\4F53';
   font-size: 18px;
}

li {
   list-style: none;
}

a {
   text-decoration: none;
   font-size: 16px;
}

a:hover {
   text-decoration: none;
   color: white;
}

input, button {
   padding: 0 15px 0;
   border: none;
   border-radius: 3px;
}

body {
   background-image: url(/static/media/bg.fdea5bbd.jpg);
   background-repeat: no-repeat;
   background-size: cover;
}

#root {
   width: 70%;
   min-width: 785px;
   margin: 0 auto;
   background-repeat: no-repeat;
}

.App {
   text-align: center;
}


.head {
   position: relative;
   padding: 0;
   height: 50px;
   width: 100%;
   background-color: rgba(255, 255, 224, .6);
   line-height: 50px;
   box-shadow: 0 2px 3px #ccc;
}

.head .title {
   position: relative;
   float: left;
   height: 100%;
   background-color: rgba(255, 255, 224, .6);
   font-size: 0;
   overflow: hidden;
}

.head .title::after{
   content: '';
   position: absolute;
   display: block;
   top: -25px;
   left: -50px;
   width: 20px;
   height: 100px;
   background-image: -webkit-linear-gradient(left, #ccc, #fff, #ccc);
   background-image: linear-gradient(to right, #ccc, #fff, #ccc);
   -webkit-transform: rotateZ(45deg);
           transform: rotateZ(45deg);
   opacity: .6;
   -webkit-animation: move 5s ease-in-out infinite;
           animation: move 5s ease-in-out infinite;
}

@-webkit-keyframes move {
   0% {
      left: -50px;
   } 
   40% {
      left: 350px;
   }
   100% {
      left: 350px;
   }
}

@keyframes move {
   0% {
      left: -50px;
   } 
   40% {
      left: 350px;
   }
   100% {
      left: 350px;
   }
}

.head .title img {
   height: 100%;
}

.head .nav { 
   margin-left: 20px;
   left: 0;
   float: left;
}

.head .nav a{
   padding: 0 10px;
   color: black;
}

.head .nav a:hover {
   color: rgba(255, 255, 224, .6);
   text-decoration: none;
   background-color: rgb(76, 175, 175)
}

.head .headlogin{
   float: right;
   margin-right: 40px;
}

.head .headlogin span:hover {
   cursor: pointer;
   color: red;
}
