.body {
   margin-top: 8px;
   background-color: rgba(255, 255, 224, .6);
   box-shadow: 0 0 3px 1px #ccc;
}


.leave-words-list {
   text-align: left;
}

.list-item {
   margin-top: 20px;
   padding: 20px;
   background: rgb(245, 241, 241);
   border-radius: 5px;
}

.list-item > div {
   position: relative;
   padding-top: 10px;
   border-bottom: 2px double #eee;
   -webkit-transition: background .5s;
   transition: background .5s;
}

.list-item > div:hover {
   background: white;
}

.join-comment {
   margin: 10px;
   padding: 10px;
   width: 90%;
   resize: none;
   border-radius: 5px;
   outline: none;
   -webkit-transition: height .3s ease-in;
   transition: height .3s ease-in;
}

.comment-submit {
   margin-left: 10px;
   margin-bottom: 5px;
   display: block;
   padding: 3px 5px;
   font-size: 14px;
   background: white;
   border-radius: 3px;
}

.join-comment:focus {
   outline: none;
}

.join-comment.active {
   -webkit-animation: prompt 3s;
           animation: prompt 3s;
}

@-webkit-keyframes prompt {
   0% {
      box-shadow: none;
   }
   50% {
      box-shadow: 0 0 3px 3px rgba(255, 22, 22, .9);
   }
   100% {
      box-shadow: none;
   }
}

@keyframes prompt {
   0% {
      box-shadow: none;
   }
   50% {
      box-shadow: 0 0 3px 3px rgba(255, 22, 22, .9);
   }
   100% {
      box-shadow: none;
   }
}

.list-item-btn {
   position: absolute;
   top: 10px;
   right: 10px;
   padding: 3px 5px;
   font-size: 14px;
   background: white;
   border-radius: 3px;
   opacity: 0;
   box-shadow: 0 0 2px 2px #ccc;
   -webkit-transition: opacity .5s, box-shadow .2s;
   transition: opacity .5s, box-shadow .2s; 
}

.list-item-btn:focus {
   outline: none;
   box-shadow: none;
}

#list-item-main:hover .list-item-btn {
   opacity: 1;
   background: #eee;
}

#list-item-main:hover #comment-submit {
   background: rgb(245, 241, 241);
}

.list-item-info {
   font-size: 14px;
}

.list-item-value {
   margin-left: 15px;
}

.leave-words-list p {
   margin-bottom: 5px;
}
