.footer {
   margin-top: 30px;
   padding: 30px;
   background: rgba(140, 196, 218, 0.5);
   /* border-top: 1px solid black; */
   box-sizing: border-box;
}

.leave-words {
   /* height: 200px; */
   width: 100%;
}

.leave-down {
   padding: 8px;
   height: 200px;
   width: 100%;
   border-radius: 5px;
   transition: all .5s;
   resize: none;
}

.leave-down:focus{
   outline: none;
}

.message {
   margin-top: 8px;
}

.message input {
   padding: 2px 10px;
   box-sizing: border-box;
}

.message input:focus {
   outline: none;
   box-shadow: 0 0 2px 2px #ccc;
}

.mes-mail {
   margin-top: 5px;
}

.submit {
   margin-top: 20px;
   background: white;
   border-radius: 3px;
}

.submit:focus {
   outline: none;
   background: #eee;
}