.head {
   position: relative;
   padding: 0;
   height: 50px;
   width: 100%;
   background-color: rgba(255, 255, 224, .6);
   line-height: 50px;
   box-shadow: 0 2px 3px #ccc;
}

.head .title {
   position: relative;
   float: left;
   height: 100%;
   background-color: rgba(255, 255, 224, .6);
   font-size: 0;
   overflow: hidden;
}

.head .title::after{
   content: '';
   position: absolute;
   display: block;
   top: -25px;
   left: -50px;
   width: 20px;
   height: 100px;
   background-image: linear-gradient(to right, #ccc, #fff, #ccc);
   transform: rotateZ(45deg);
   opacity: .6;
   animation: move 5s ease-in-out infinite;
}

@keyframes move {
   0% {
      left: -50px;
   } 
   40% {
      left: 350px;
   }
   100% {
      left: 350px;
   }
}

.head .title img {
   height: 100%;
}

.head .nav { 
   margin-left: 20px;
   left: 0;
   float: left;
}

.head .nav a{
   padding: 0 10px;
   color: black;
}

.head .nav a:hover {
   color: rgba(255, 255, 224, .6);
   text-decoration: none;
   background-color: rgb(76, 175, 175)
}

.head .headlogin{
   float: right;
   margin-right: 40px;
}

.head .headlogin span:hover {
   cursor: pointer;
   color: red;
}